/*
 * If not building with SSR mode, you can
 * directly export the Router instantiation;
 *
 * The function below can be async too; either use
 * async/await or return a Promise which resolves
 * with the Router instance.
 */

export default async function ({ store, ssrContext }) {
	let mainRoute = routes.find(x => x.name = "main");

	
	let loginToken = store.getters["auth/getLoginToken"];
	if(loginToken){
		try{
			/*
			 * get current user data
			 * user data can be access anywhere using auth store
			*/
			await store.dispatch('auth/getUserData');
			let userRoles = store.state.auth.userRoles;
			let defaultRole = (userRoles[0] || "").toLowerCase();
			switch(defaultRole){
				
				case "administrator":
					mainRoute.children.push({ 
						path: '/(home)?', 
						name: 'administrator', 
						component: () => import('pages/home/administrator.vue'),
						props: true
					});
				
				default:
					mainRoute.children.push({ 
						path: '/(index|home)?', 
						name: 'home', 
						component: () => import('pages/home/home.vue'),
						props: true
					});
			}
		}
		catch(e){
			/*
			 * getting current user detail failed
			 * token must be invalid
			*/
			mainRoute.children.push({ 
				path: '/(index|home)?', 
				name: 'index', 
				component: () => import('pages/index/index.vue'),
				props: true 
			});
		}
	}
	else{
		mainRoute.children.push({ 
			path: '/(index|home)?', 
			name: 'index', 
			component: () => import('pages/index/index.vue'),
			props: true
		});
	}


	// Always leave this as last one
	if (process.env.MODE !== 'ssr') {
		mainRoute.children.push({path: '*', component: () => import('pages/errors/pagenotfound.vue')});
	}

	const Router = new VueRouter({
		scrollBehavior: () => ({ x: 0, y: 0 }),
		routes,
		// Leave these as they are and change in quasar.conf.js instead!
		// quasar.conf.js -> build -> vueRouterMode
		// quasar.conf.js -> build -> publicPath
		mode: process.env.VUE_ROUTER_MODE,
		base: process.env.VUE_ROUTER_BASE
	});
	return Router
}
